import { Formik } from 'formik';
import { object, string, number } from 'yup';
import { ContForm, Input, SubmitButton, Error } from './ContactForm.styled';
import { useDispatch, useSelector } from 'react-redux';
import { addContact } from 'redux/operations';
import { toast } from 'react-toastify';
import { selectContacts } from 'redux/selectors';

const validationSchema = object({
  name: string().min(4).required(),
  phone: number().min(4).required(),
});

const ContactForm = () => {
  const dispatch = useDispatch();
  const contacts = useSelector(selectContacts);

  const handleSubmit = (values, { resetForm }) => {

    contacts.find(contact => contact.name.toLowerCase() === values.name.toLowerCase()) ?
      toast.error(`${values.name} is already in contacts`) :
      dispatch(addContact(values));

    resetForm();
  };

  return (
    <Formik
      initialValues={{
        name: "",
        phone: ""
      }}
      onSubmit={handleSubmit}
      validationSchema={validationSchema}
    >
      <ContForm>
        <label htmlFor="name">
          Name
          <Input
            type="text"
            name="name"
            pattern="^[a-zA-Z--]+(([' \-][a-zA-Z--])?[a-zA-Z--]*)*$"
            title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
            required
          />
          <Error component="p" name="name" />
        </label>
        <label htmlFor="number">
          Number
          <Input
            type="tel"
            name="phone"
            pattern="\+?\d{1,4}?[\-.\s]?\(?\d{1,3}?\)?[\-.\s]?\d{1,4}[\-.\s]?\d{1,4}[\-.\s]?\d{1,9}"
            title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
            required
          />
          <Error component="p" name="number" />
        </label>
        <SubmitButton type="submit">Add contact</SubmitButton>
      </ContForm>
    </Formik>
  );
};

export default ContactForm;